import React from "react";

const imgLink = "https://picsum.photos/200/200?grayscale";

function App() {
  return (
    <>
      <h1>This is the H1 Tag</h1>
      <ul>
        <li contentEditable="true" spellcheck="true">
          This is some editable text and chceks yuor seplling! Try it!
        </li>
        <a href="#" accessKey="h" translate="no">
          This cannot be translated: Lorem ipsum dolor sit amet.
        </a>
        <li>There is a hidden text below here!</li>
        <li hidden>Hidden Text</li>
        <li>
          IE, Chrome, Safari, Opera 15+: [ALT] + <em>h</em>
        </li>
        <li>
          Opera prior version 15: [SHIFT] [ESC] + <em>h</em>
        </li>
        <li>
          Firefox: [ALT] [SHIFT] + <em>h</em>
        </li>
        <li>
          The Image below is <em>draggable</em>
        </li>

        <li>
          <img src={imgLink} draggable="true"></img>
        </li>
      </ul>
      <button draggable="false">This button cannot be dragged</button>
    </>
  );
}

export default App;

